.card {
  background: #fff;
  border-radius: 2px;
  margin: 1rem;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  overflow: hidden;
  height: 200px;
  width: 200px;
}

.card > .img-container > img {
  -o-object-fit: cover;
     object-fit: cover;
  height: 100%;
  width: 100%;
}


